import config from "./config.js";

// token
// const TOKEN = uni.getStorageSync("token");
/**
 *  
 * @param {*} options
 */
let Interceptor = (options) => {
  options.url = config.baseURL + options.url;
  options.dataType = options.dataType || "json";
  options.data = {
    ...options.data,
  };
  options.header = {
    // token: TOKEN,
    "content-type": "application/json",
    ...options.header,
  };
  options.method = options.method;

  //loadingtrue,loadding
  if (options.isloading) {
    uni.showLoading({
      title: "",
      mask: true,
    });
  }
  return options;
};

/**
 * 
 * @param {*} res
 */
let Responder = (res) => {
  uni.hideLoading();
  if (res && res.data) {
    switch (res.data.code) {
      case 200:
        return Promise.resolve(res.data);
      case 400:
        uni.showToast({
          icon: "error",
          title: "",
          duration: 2000,
        });
        return Promise.reject({
          msg: "",
        });
      case 403:
        uni.reLaunch({
          url: "/pages/login/login",
        });
        uni.showToast({
          icon: "error",
          title: "",
          duration: 2000,
        });
        return Promise.reject({
          msg: "",
        });
      case 404:
        uni.showToast({
          icon: "error",
          title: "",
          duration: 2000,
        });
        return Promise.reject({
          msg: "",
        });
      default:
        uni.showToast({
          icon: "error",
          title: `${res.data.code}`,
          duration: 2000,
        });
        return Promise.reject(res.data);
    }
  }
};

/**
 * 
 *
 * */
let request = (options = {}) => {
  options = Interceptor(options);
  //
  return new Promise((resolve, reject) => {
    // 
    uni.getNetworkType({
      success(res) {
        if (res.networkType == "none") {
          uni.showModal({
            title: "",
            content: "",
            showCancel: false,
            success: (res) => {
              uni.hideLoading();
            },
          });
        } else {
          uni.request({
            ...options,
            success: (res) => {
              resolve(Responder(res));
            },
            fail: (err) => {
              reject(err);
            },
          });
        }
      },
    });
  });
};

export default {
  get(url, data, options = {}) {
    options.url = url;
    options.data = data;
    options.method = "GET";
    return request(options);
  },
  post(url, data, options = {}) {
    options.url = url;
    options.data = data;
    options.method = "POST";
    return request(options);
  },
  put(url, data, options = {}) {
    options.url = url;
    options.data = data;
    options.method = "PUT";
    return request(options);
  },
  delete(url, data, options = {}) {
    options.url = url;
    options.data = data;
    options.method = "DELETE";
    return request(options);
  },
};
